'use client';

import { useState } from 'react';
import {
  Plus,
  Search,
  Filter,
  MoreHorizontal,
  Package,
  Edit,
  Trash2,
  Eye,
  Download,
  Upload,
  Barcode,
} from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { formatCurrency } from '@/lib/utils';

// Demo data
const products = [
  {
    id: '1',
    name: 'Süt 1L',
    sku: 'SKU-001',
    barcode: '8690000000001',
    category: 'Süt Ürünleri',
    stock: 45,
    minStock: 20,
    purchasePrice: 12.50,
    salePrice: 15.00,
    vatRate: 10,
    unit: 'Adet',
    status: 'active',
  },
  {
    id: '2',
    name: 'Beyaz Peynir 500g',
    sku: 'SKU-002',
    barcode: '8690000000002',
    category: 'Süt Ürünleri',
    stock: 8,
    minStock: 15,
    purchasePrice: 45.00,
    salePrice: 55.00,
    vatRate: 10,
    unit: 'Adet',
    status: 'low_stock',
  },
  {
    id: '3',
    name: 'Ekmek',
    sku: 'SKU-003',
    barcode: '8690000000003',
    category: 'Fırın Ürünleri',
    stock: 120,
    minStock: 50,
    purchasePrice: 5.00,
    salePrice: 7.50,
    vatRate: 1,
    unit: 'Adet',
    status: 'active',
  },
  {
    id: '4',
    name: 'Domates 1kg',
    sku: 'SKU-004',
    barcode: '8690000000004',
    category: 'Meyve Sebze',
    stock: 35,
    minStock: 20,
    purchasePrice: 18.00,
    salePrice: 24.90,
    vatRate: 1,
    unit: 'Kg',
    status: 'active',
  },
  {
    id: '5',
    name: 'Ayçiçek Yağı 2L',
    sku: 'SKU-005',
    barcode: '8690000000005',
    category: 'Temel Gıda',
    stock: 0,
    minStock: 10,
    purchasePrice: 85.00,
    salePrice: 99.90,
    vatRate: 10,
    unit: 'Adet',
    status: 'out_of_stock',
  },
  {
    id: '6',
    name: 'Yumurta 30lu',
    sku: 'SKU-006',
    barcode: '8690000000006',
    category: 'Temel Gıda',
    stock: 25,
    minStock: 15,
    purchasePrice: 75.00,
    salePrice: 89.90,
    vatRate: 1,
    unit: 'Koli',
    status: 'active',
  },
];

const categories = ['Tümü', 'Süt Ürünleri', 'Fırın Ürünleri', 'Meyve Sebze', 'Temel Gıda', 'İçecekler'];

const statusLabels: Record<string, { label: string; color: string }> = {
  active: { label: 'Aktif', color: 'bg-emerald-100 text-emerald-700' },
  low_stock: { label: 'Düşük Stok', color: 'bg-orange-100 text-orange-700' },
  out_of_stock: { label: 'Stok Yok', color: 'bg-red-100 text-red-700' },
  inactive: { label: 'Pasif', color: 'bg-gray-100 text-gray-700' },
};

export default function ProductsPage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('Tümü');

  const filteredProducts = products.filter((product) => {
    const matchesSearch =
      product.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      product.sku.toLowerCase().includes(searchQuery.toLowerCase()) ||
      product.barcode.includes(searchQuery);
    const matchesCategory =
      selectedCategory === 'Tümü' || product.category === selectedCategory;
    return matchesSearch && matchesCategory;
  });

  const stats = {
    total: products.length,
    active: products.filter((p) => p.status === 'active').length,
    lowStock: products.filter((p) => p.status === 'low_stock').length,
    outOfStock: products.filter((p) => p.status === 'out_of_stock').length,
  };

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Ürünler</h1>
          <p className="text-muted-foreground">
            Ürün ve stok yönetimi
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Upload className="h-4 w-4 mr-2" />
            İçe Aktar
          </Button>
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Dışa Aktar
          </Button>
          <Button size="sm">
            <Plus className="h-4 w-4 mr-2" />
            Yeni Ürün
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid gap-4 md:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-muted-foreground">
              Toplam Ürün
            </CardTitle>
            <Package className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.total}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-muted-foreground">
              Aktif Ürün
            </CardTitle>
            <div className="h-2 w-2 rounded-full bg-emerald-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-emerald-600">{stats.active}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-muted-foreground">
              Düşük Stok
            </CardTitle>
            <div className="h-2 w-2 rounded-full bg-orange-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{stats.lowStock}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <CardTitle className="text-sm font-medium text-muted-foreground">
              Stok Yok
            </CardTitle>
            <div className="h-2 w-2 rounded-full bg-red-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{stats.outOfStock}</div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
            <div className="flex flex-1 gap-4">
              <div className="relative flex-1 max-w-sm">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                <Input
                  type="search"
                  placeholder="Ürün, SKU veya barkod ara..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-9"
                />
              </div>
              <div className="flex gap-2">
                {categories.map((category) => (
                  <Button
                    key={category}
                    variant={selectedCategory === category ? 'default' : 'outline'}
                    size="sm"
                    onClick={() => setSelectedCategory(category)}
                    className="whitespace-nowrap"
                  >
                    {category}
                  </Button>
                ))}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Products Table */}
      <Card>
        <CardContent className="pt-6">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead className="w-[300px]">Ürün</TableHead>
                <TableHead>SKU</TableHead>
                <TableHead>Barkod</TableHead>
                <TableHead>Kategori</TableHead>
                <TableHead className="text-right">Stok</TableHead>
                <TableHead className="text-right">Alış Fiyatı</TableHead>
                <TableHead className="text-right">Satış Fiyatı</TableHead>
                <TableHead>Durum</TableHead>
                <TableHead className="w-[80px]">İşlem</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredProducts.map((product) => (
                <TableRow key={product.id}>
                  <TableCell>
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 rounded-lg bg-muted flex items-center justify-center">
                        <Package className="h-5 w-5 text-muted-foreground" />
                      </div>
                      <div>
                        <p className="font-medium">{product.name}</p>
                        <p className="text-xs text-muted-foreground">
                          KDV: %{product.vatRate} • {product.unit}
                        </p>
                      </div>
                    </div>
                  </TableCell>
                  <TableCell className="font-mono text-sm">{product.sku}</TableCell>
                  <TableCell>
                    <div className="flex items-center gap-1">
                      <Barcode className="h-3 w-3 text-muted-foreground" />
                      <span className="font-mono text-sm">{product.barcode}</span>
                    </div>
                  </TableCell>
                  <TableCell>{product.category}</TableCell>
                  <TableCell className="text-right">
                    <div className="flex flex-col items-end">
                      <span className={product.stock <= product.minStock ? 'text-orange-600 font-medium' : ''}>
                        {product.stock}
                      </span>
                      <span className="text-xs text-muted-foreground">
                        min: {product.minStock}
                      </span>
                    </div>
                  </TableCell>
                  <TableCell className="text-right font-mono">
                    {formatCurrency(product.purchasePrice)}
                  </TableCell>
                  <TableCell className="text-right font-mono font-medium">
                    {formatCurrency(product.salePrice)}
                  </TableCell>
                  <TableCell>
                    <span
                      className={`inline-flex items-center rounded-full px-2 py-1 text-xs font-medium ${
                        statusLabels[product.status].color
                      }`}
                    >
                      {statusLabels[product.status].label}
                    </span>
                  </TableCell>
                  <TableCell>
                    <div className="flex items-center gap-1">
                      <Button variant="ghost" size="icon" className="h-8 w-8">
                        <Eye className="h-4 w-4" />
                      </Button>
                      <Button variant="ghost" size="icon" className="h-8 w-8">
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button variant="ghost" size="icon" className="h-8 w-8 text-destructive">
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>

          {filteredProducts.length === 0 && (
            <div className="flex flex-col items-center justify-center py-12 text-center">
              <Package className="h-12 w-12 text-muted-foreground mb-4" />
              <h3 className="text-lg font-medium">Ürün bulunamadı</h3>
              <p className="text-muted-foreground">
                Arama kriterlerinize uygun ürün bulunmamaktadır.
              </p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
